import { ServerFile } from "@rp/common/utils"
import { generateId } from "./generateId"
import { userCollaborator, userOwner } from "./users"

type Props = { id?: string; name: string; owner?: boolean; sharedWith?: Array<string> | null }

export const generateServerFile = (params: Props): ServerFile => {
  const { id = generateId(), name, owner = true, sharedWith = null } = params
  const [upload_ts] = new Date().toISOString().split(".") ?? []
  return {
    file_id: id,
    upload_ts,
    status: "COMPLETED",
    file_name: name,
    raw_size: 200,
    trashed: false,
    folder_id: null,
    error: null,
    tags: null,
    shared_with: sharedWith,
    owner: owner ? userOwner.ownerUserId : userCollaborator.ownerUserId,
  }
}
